// API 
const BASE_URL = 'http://localhost:8080/api'

class Request {
  constructor() {
    this.baseURL = BASE_URL
    this.timeout = 10000
  }
  
  // 
  request(options) {
    return new Promise((resolve, reject) => {
      // token
      let token = ''
      try {
        token = uni.getStorageSync('token') || ''
      } catch (error) {
        console.error('token:', error)
      }
      
      // 
      const header = {
        'Content-Type': 'application/json',
        ...options.header
      }
      
      if (token) {
        header.Authorization = `Bearer ${token}`
      }
      
      uni.request({
        url: this.baseURL + options.url,
        method: options.method || 'GET',
        data: options.data || {},
        header,
        timeout: this.timeout,
        success: (res) => {
          console.log('API:', res)
          
          if (res.statusCode === 200) {
            resolve(res.data)
          } else if (res.statusCode === 401) {
            // token
            try {
              uni.removeStorageSync('token')
            } catch (error) {
              console.error('token:', error)
            }
            uni.navigateTo({
              url: '/pages/login/index'
            })
            reject(new Error(''))
          } else {
            reject(new Error(res.data?.message || ''))
          }
        },
        fail: (err) => {
          console.error('API:', err)
          uni.showToast({
            title: '',
            icon: 'error'
          })
          reject(err)
        }
      })
    })
  }
  
  // GET
  get(url, data = {}) {
    return this.request({
      url,
      method: 'GET',
      data
    })
  }
  
  // POST
  post(url, data = {}) {
    return this.request({
      url,
      method: 'POST',
      data
    })
  }
  
  // PUT
  put(url, data = {}) {
    return this.request({
      url,
      method: 'PUT',
      data
    })
  }
  
  // DELETE
  delete(url, data = {}) {
    return this.request({
      url,
      method: 'DELETE',
      data
    })
  }
}

export default new Request()
